@media (min-width: 1024px) and ( max-width: 1279px)  {
    .contactus{
        .contact-page {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          width: 100vw;
          min-height: 100vh;
        }
        
        .contact-content-container {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            gap: 10px;
            padding-top: 50px;
            padding-bottom: 50px; 
        }
        
        .ccc-left {  
          box-sizing: border-box;
          padding: 40px;
          text-align: start;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start; 
          text-align: center;
        }
        
        .ccc-right {
          width: 600px;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          align-items: center;  
        }
        .contact-form {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          padding: 0px 0px 0px 0px;
          margin: 0px auto;
          width: 100%;
        }
        
        .form-holder {
          width: 100%;
          border-radius: 1px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          margin: 0px auto;
          padding: 0px auto;
        }
        .grey-area {
          max-width: 100%;
          width: 90%;
          margin: 28px 8px 8px 8px;
          border-radius: 1px;
          padding: 2rem 2rem;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
        .form-factory {
          text-align: start;
          width: 100%;
          padding: 0px;
        }
        
        .form-elements {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: flex-start;
          width: 100%;
        }
        textarea {
          border: none;
          padding: 8px;
          margin: 0px auto;
          resize: none !important;
          background: none;
          outline-width: 0px;
          outline: none;
          border-radius: 1px;
          min-width: 10%;
          box-sizing: border-box;
          width: 100%; 
          background-color: #404462;
        }
        label {
          font-size: 14px;
          margin-bottom: 4px;
          margin-top: 10px;
          margin-bottom: 10px;
          padding: 0px auto;
          text-align: start;
          width: 100%;
        }
        input[type="text"] {
          min-width: 10%;
          height: 4%;
          min-height: 40px;
          padding: 6px;
          box-sizing: border-box;
          margin: 0px auto;
          border: none;
          border-radius: 1px;
          outline-width: 0px;
          outline: none;
          width: 100%;
        }
        input:focus {
          outline-width: 0;
          outline: none;
        }
        .cf-button_row {
          width: 100%;
          margin: 20px;
          display: flex;
          flex-direction: row;
          justify-content: center;
        }
        .btn-send {
          width: fit-content;
          background: none;
          border: none;
          font-weight: 900;
          margin-left: 40%;
          cursor: pointer;
        }
        
        .contact_socials {
          display: flex;
          flex-direction: row;
          justify-content: space-around;
          align-items: center;
          gap: 5px;
          margin-top: 30px;
          width: 100%;
        }
      }
      
      //projects page 
.portfolio-page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: ease-in-out 0.5s;  

  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 4rem;
    padding-top: 30vh;
    .content-head {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      max-width: 100%;
      text-align: center; 
    }
    .content-menu {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      gap: .5rem;
      border-top: 1px solid #123;
      border-bottom: 1px solid #123;
      width: 100%;
      .menu-projects {
        padding: 1.5rem 1.5rem;
        cursor: pointer;
        &:hover {
          transition: ease-in-out 0.5s;
          background-color: #161d2e;
        }
      }
      .filter-active {
        color: white;
      }
    }

    .content-works {  
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 100%;
      .content-grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        width: 100%;
        max-width: 100%;
        gap: 2rem;
        .work-item {
          max-width: 90vw;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center; 
          .work-content {
            display: flex;
            flex-direction: column;
            height: 100%;
            gap: 1rem;
            justify-content: space-between;
            .work-header {
              padding: 2rem 2rem;
              min-height: 250px;
              width: 100%;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              text-align: center;
              gap: 10px;
              .work-item_tools {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                gap: 1rem;
                justify-content: space-around;
              }
            }
            .work-body {
              padding: 2rem 2rem;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              flex-grow: 1;
            }
            .work-footer {
              border-top: 1px solid #2c314b;
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              align-items: center;

              .work-footer_left {
                padding: 1rem 2rem;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                gap: 2rem;
                .work-link_live {
                  display: flex;
                  flex-direction: row;
                  gap: 5px;
                }
              }

              .work-github {
                padding: 1rem 2rem;
                border-left: 1px solid #2c314b;
              }
            }
          }
        }
      }
    }
  }
}       
}